"use client";

import { useState, useEffect } from "react";
import {
  CalendarDays,
  MapPin,
  ArrowUpRight,
  X,
  Clock,
  MoveRight,
} from "lucide-react";
// Import the separated data and type
import { events } from "@/data/events";

export default function SwissEvents() {
  const [selectedEvent, setSelectedEvent] = useState<Event | null>(null);

  // Prevent scrolling when modal is open
  useEffect(() => {
    if (selectedEvent) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "unset";
    }
    return () => {
      document.body.style.overflow = "unset";
    };
  }, [selectedEvent]);

  const formatDate = (dateStr: string) => {
    const d = new Date(dateStr);
    const date = new Date(d.valueOf() + d.getTimezoneOffset() * 60 * 1000);
    return {
      day: date.getDate(),
      month: date.toLocaleString("default", { month: "short" }),
      fullMonth: date.toLocaleString("default", { month: "long" }),
      weekday: date.toLocaleString("default", { weekday: "long" }),
    };
  };

  return (
    <section className="min-h-screen bg-[#F4F4F5] text-zinc-900 py-24 px-4 font-sans selection:bg-zinc-900 selection:text-white">
      <div className="max-w-[1400px] mx-auto">
        {/* --- Header --- */}
        <div className="flex flex-col md:flex-row md:items-end justify-between mb-16 gap-8">
          <div>
            <div className="flex items-center gap-2 mb-4">
              <span className="w-2 h-2 rounded-full bg-zinc-900 animate-pulse" />
              <h5 className="text-xs font-bold uppercase tracking-[0.2em] text-zinc-500">
                Agenda 2025
              </h5>
            </div>
            <h2 className="text-6xl md:text-8xl font-bold tracking-tighter leading-[0.85]">
              Upcoming
              <span className="block text-zinc-400">EVENTS.</span>
            </h2>
          </div>

          <div className="hidden md:block">
            <button className="group flex items-center gap-3 px-8 py-4 bg-white rounded-full border border-zinc-200 hover:border-zinc-900 transition-all duration-300">
              <span className="text-sm font-bold tracking-wide">
                Full Calendar
              </span>
              <ArrowUpRight className="w-4 h-4 transition-transform group-hover:-translate-y-0.5 group-hover:translate-x-0.5" />
            </button>
          </div>
        </div>

        {/* --- Grid --- */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          {events.map((event) => {
            const { day, month } = formatDate(event.date);

            return (
              <article
                key={event.id}
                onClick={() => setSelectedEvent(event)}
                className="group relative flex flex-col h-[500px] bg-white rounded-[2.5rem] p-2 cursor-pointer transition-all duration-500 hover:-translate-y-2 hover:shadow-2xl hover:shadow-zinc-200/50"
              >
                <div className="flex flex-col h-full w-full rounded-[2rem] bg-zinc-50/50 border border-zinc-100 p-8 transition-colors duration-500 group-hover:bg-white">
                  {/* Top: Category & Date */}
                  <div className="flex justify-between items-start mb-auto">
                    <span className="px-4 py-2 rounded-full bg-white border border-zinc-200 text-[11px] font-bold uppercase tracking-wider text-zinc-600 shadow-sm group-hover:border-zinc-900 group-hover:text-zinc-900 transition-colors">
                      {event.category}
                    </span>
                    <div className="flex flex-col items-end">
                      <span className="text-sm font-bold text-zinc-400 uppercase tracking-widest">
                        {month}
                      </span>
                      <span className="text-5xl font-bold tracking-tighter leading-none text-zinc-900">
                        {day}
                      </span>
                    </div>
                  </div>

                  {/* Middle: Content */}
                  <div className="space-y-4 mb-12">
                    <h3 className="text-3xl font-bold leading-tight tracking-tight group-hover:underline decoration-2 underline-offset-4 decoration-zinc-200">
                      {event.title}
                    </h3>
                    <p className="text-zinc-500 text-sm leading-relaxed line-clamp-2">
                      {event.description}
                    </p>
                  </div>

                  {/* Bottom: Action */}
                  <div className="mt-auto flex items-center justify-between pt-6 border-t border-zinc-200/60">
                    <div className="flex items-center gap-2 text-xs font-bold text-zinc-400 uppercase tracking-wider">
                      <Clock className="w-4 h-4" />
                      {event.time}
                    </div>

                    <div
                      className={`w-12 h-12 rounded-full ${event.accent} flex items-center justify-center text-white transition-transform duration-500 group-hover:scale-110 group-hover:rotate-45`}
                    >
                      <ArrowUpRight className="w-5 h-5" />
                    </div>
                  </div>
                </div>
              </article>
            );
          })}
        </div>
      </div>

      {/* --- Detailed Modal Popup --- */}
      {selectedEvent && (
        <div
          className="fixed inset-0 z-[999] flex items-center justify-center p-4 md:p-8"
          role="dialog"
          aria-modal="true"
        >
          <div
            className="absolute inset-0 bg-zinc-200/60 backdrop-blur-md animate-in fade-in duration-300"
            onClick={() => setSelectedEvent(null)}
          />

          <div className="relative w-full max-w-4xl bg-white rounded-[3rem] shadow-2xl overflow-hidden animate-in zoom-in-95 slide-in-from-bottom-4 duration-500 flex flex-col md:flex-row max-h-[90vh] md:max-h-[600px]">
            {/* Left Panel */}
            <div
              className={`relative md:w-2/5 p-10 flex flex-col justify-between ${selectedEvent.accent} text-white`}
            >
              <div className="relative z-10">
                <p className="opacity-80 font-medium tracking-widest uppercase text-xs mb-2">
                  {selectedEvent.category}
                </p>
                <h2 className="text-4xl md:text-5xl font-bold tracking-tighter leading-tight">
                  {formatDate(selectedEvent.date).fullMonth} <br />
                  {formatDate(selectedEvent.date).day}
                </h2>
                <p className="mt-2 opacity-80 font-medium">
                  {formatDate(selectedEvent.date).weekday}
                </p>
              </div>

              <div className="absolute -bottom-20 -right-20 w-64 h-64 bg-white/10 rounded-full blur-2xl" />
              <div className="absolute top-10 right-10 w-32 h-32 bg-white/10 rounded-full blur-xl" />

              <div className="relative z-10 mt-12 md:mt-0">
                <div className="flex items-center gap-3 opacity-90">
                  <MapPin className="w-5 h-5" />
                  <span className="text-sm font-bold tracking-wide">
                    {selectedEvent.location}
                  </span>
                </div>
              </div>
            </div>

            {/* Right Panel */}
            <div className="flex-1 p-10 md:p-12 overflow-y-auto bg-white">
              <div className="flex justify-between items-start mb-8">
                <div>
                  <div className="flex items-center gap-2 mb-2">
                    <span
                      className={`w-2 h-2 rounded-full ${selectedEvent.accent}`}
                    ></span>
                    <span className="text-xs font-bold text-zinc-400 uppercase tracking-widest">
                      Event Details
                    </span>
                  </div>
                  <h3 className="text-3xl md:text-4xl font-bold text-zinc-900 tracking-tighter mb-1">
                    {selectedEvent.title}
                  </h3>
                </div>
                <button
                  onClick={() => setSelectedEvent(null)}
                  className="p-2 rounded-full bg-zinc-100 text-zinc-500 hover:bg-zinc-200 hover:text-zinc-900 transition-colors"
                >
                  <X className="w-6 h-6" />
                </button>
              </div>

              <div className="space-y-8">
                <p className="text-zinc-600 text-lg leading-relaxed">
                  {selectedEvent.description}
                </p>

                <div className="grid grid-cols-2 gap-4">
                  <div className="p-6 rounded-[1.5rem] bg-zinc-50 border border-zinc-100">
                    <Clock className="w-6 h-6 text-zinc-400 mb-3" />
                    <p className="text-xs font-bold uppercase tracking-wider text-zinc-400 mb-1">
                      Start Time
                    </p>
                    <p className="text-lg font-bold text-zinc-900">
                      {selectedEvent.time}
                    </p>
                  </div>
                  <div className="p-6 rounded-[1.5rem] bg-zinc-50 border border-zinc-100">
                    <CalendarDays className="w-6 h-6 text-zinc-400 mb-3" />
                    <p className="text-xs font-bold uppercase tracking-wider text-zinc-400 mb-1">
                      Date
                    </p>
                    <p className="text-lg font-bold text-zinc-900">
                      {selectedEvent.date}
                    </p>
                  </div>
                </div>

                <div className="pt-4">
                  <button
                    className={`w-full py-5 rounded-2xl ${selectedEvent.accent} text-white font-bold text-base hover:opacity-90 transition-opacity flex items-center justify-between px-8 group`}
                  >
                    <span>Register Now</span>
                    <MoveRight className="w-5 h-5 transition-transform group-hover:translate-x-1" />
                  </button>
                  <p className="text-center text-xs text-zinc-400 mt-4 font-medium">
                    Limited spots available for this session.
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </section>
  );
}
